.header {
    height: 100px;
    max-width: 1200px;
    width: 50%;
    display: flex;
    padding: 5% 1%;
    background-color: transparent;
    margin: auto;
  }
  
  .header a {
    font-size: 2vh;
    text-decoration: none;
    letter-spacing: 4.8px;
    height: fit-content;
    font-size:24px;
    font-family: 'Roboto', sans-serif;
  }
  
  .header a:hover {
    border-bottom: 1px solid white;
    transition-duration: 0.5s;
  }
  
  .header a:visited {
    color:white;
  }
  

@media screen and (max-width: 1024px) {
  .header {
    width: 85%;
    padding: 5%;
  } 
}
