.main {
    color: white;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    align-items: flex-start;
    width:50%;
    max-width: 1200px;
    margin: 15vh auto;
    padding: 1%;
    background-color: rgba(0, 0, 0, 0.5);
}

.main * {
    margin: auto;
    justify-content: center;
}

.main h2 {
    text-align: center;
    font-size: 48px;
    width: 100%;
}

.main p {
    font-size: 28px;
    width: 100%;
    line-height: 64px;
    text-align: center;
}


@media screen and (min-width: 800px) and (max-width: 1024px){
    .main {
        width: 75%;
        line-height: 100%;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .main h2 {
        font-size: 28px;
        line-height: 56px;
    }

    .main p {
        font-size: 24px;
        line-height: 72px;
        text-align: center;
    }

}

@media screen and (max-width: 800px) {
    .main {
        width: 75%;
        line-height: 100%;
        font-size: 4vh;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .main h2 {
        font-size: 24px;
    }
    
    .main p {
        font-size: 16px;
        line-height: 24px;
    }
}