.contactsPageMain {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.contactsPage {
    width: 50%;
    margin: auto;
    padding: 1%;
    display: flex;
    height: fit-content;
    flex-wrap: wrap;
    justify-content: space-around;
}

.contactsPage a {
    color: white;
    text-decoration: none;
    letter-spacing: 4.8px;
}

.contactsPage a:hover {
    border-bottom: 1px solid white;
    transition-duration: 0.5s;
}

.contactsPage p {
    color:rgb(239, 238, 254);
    font-size: 1.4vh;
    line-height: 1.5vh;
}

.contactsPage h4 {
    font-size: 1.5vh;
    line-height: 1.5vh;
}

.aContact {
    flex: 1 0 25%;
    flex-wrap: wrap;

}

.aContact:last-child{
    text-align: center;
}


.aContact:last-of-type h4 {
    text-align: center;
}

.aContact h4 {
    text-align: left;
}

@media screen and (max-width: 800px) {
    .contactsPage {
        width: 75%;
        line-height: 100%;
        font-size: 1.5vh;
    }
    .aContact {
        flex: 1 -1 33%;
        flex-wrap: wrap;
        margin: 0 5px;
    }
}

    @media screen and (min-width:800px) and (max-width: 1024px) {
        .contactsPage {
            width: 85%;
            font-size: 1.5vh;
        }
        .aContact {
            flex: 1 0 33%;
            flex-wrap: wrap;
        }

}