.aboutPage {
    background-color: rgb(41, 39, 65);
    height: fit-content;
    display: flex;
    flex-direction: column;
}

.aboutPage p {
    color:rgb(239, 238, 254);
    font-size: 1.65vh;
    line-height: 3vh;
    margin: 0.25%;
}

.section {
    margin:auto;
    width: 50%;
    padding: 1%;
}

.aboutPage h2 {
    font-size: 3vh;
    text-align: center;
    margin-bottom: 0;
}

.aboutPage h3 {
    font-size: 2vh;
}

.services {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 0 2.5% 1% 2.5%;
    width: 50%;
    margin: auto;
    margin-top: 0;
}

.service {
    background-color: rgb(58,56,89);
    padding: 1% 2.5%;
    flex: 0 0 40%;
    border: 1px solid transparent;
    margin: 10px auto;
    border-radius: 30px;
}

@media screen and (max-width: 800px) {
    .section {
        width: 75%;
        font-size: 36px;
    }

    .services {
        width: 75%;
    }

    .service {
        width: 100%;
        flex: 1 0 100%;
    }

    .service h3 {
        margin-bottom: 0;
    }

    .service p {
        line-height: 1.35;
    }

    .service{
        border-radius: 15px;
    }
}

@media screen and (max-width: 1024px) and (min-width: 800px) {
    .section {
        width: 85%;
        font-size: 36px;
    }

    .services {
        width: 85%;
    }

    .service {
        width: 100%;
        flex: 1 -1 33%;
    }

    .service h3 {
        margin-bottom: 0;
    }
}